import React, { useState, useEffect, useCallback } from 'react';
import { useNavigate } from 'react-router-dom';
import { apiGet, apiPost } from '../api/api';

//  SVG 
const BellIcon = () => (
  <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
    <path d="M18 8A6 6 0 0 0 6 8c0 7-3 9-3 9h18s-3-2-3-9"></path>
    <path d="M13.73 21a2 2 0 0 1-3.46 0"></path>
  </svg>
);

//   
const timeSince = (date) => {
  const seconds = Math.floor((new Date() - new Date(date)) / 1000);
  let interval = seconds / 31536000;
  if (interval > 1) return Math.floor(interval) + " ";
  interval = seconds / 2592000;
  if (interval > 1) return Math.floor(interval) + " ";
  interval = seconds / 86400;
  if (interval > 1) return Math.floor(interval) + " ";
  interval = seconds / 3600;
  if (interval > 1) return Math.floor(interval) + " ";
  interval = seconds / 60;
  if (interval > 1) return Math.floor(interval) + " ";
  return Math.floor(seconds) + " ";
};

const NotificationBell = ({user}) => {
  const navigate = useNavigate();
  const [alerts, setAlerts] = useState([]);
  const [unreadCount, setUnreadCount] = useState(0);
  const [isOpen, setIsOpen] = useState(false);
  const [loading, setLoading] = useState(false);

  // 1.      (30)
  const fetchUnreadCount = useCallback(async () => {
    if (!user) return;
    try {
      // (GET /api/v1/users/alerts/?read=false)
      const unreadAlerts = await apiGet('/users/alerts/?read=false');
      setUnreadCount(unreadAlerts.length);
    } catch (error) {
      console.error("Failed to fetch unread alert count:", error);
    }
  }, [user]);

  useEffect(() => {
    fetchUnreadCount();
    const interval = setInterval(fetchUnreadCount, 30000); // 30 
    return () => clearInterval(interval);
  }, [fetchUnreadCount]);

  // 2.       
  const fetchAllAlerts = async () => {
    if (!user) return;
    setLoading(true);
    try {
      // (GET /api/v1/users/alerts/)
      const allAlerts = await apiGet('/users/alerts/');
      setAlerts(allAlerts);
    } catch (error) {
      console.error("Failed to fetch all alerts:", error);
    } finally {
      setLoading(false);
    }
  };

  // 3.   
  const toggleDropdown = () => {
    if (!isOpen) {
      // ,     
      fetchAllAlerts();
    }
    setIsOpen(!isOpen);
  };

  // 4.    (  +  )
  const handleAlertClick = async (alert) => {
    // 1.   (   )
    if (!alert.is_read) {
      try {
        // (POST /api/v1/users/alerts/<pk>/read/)
        await apiPost(`/users/alerts/${alert.id}/read/`);
        //   
        setUnreadCount(prev => prev - 1);
        setAlerts(prevAlerts => 
          prevAlerts.map(a => a.id === alert.id ? { ...a, is_read: true } : a)
        );
      } catch (err) {
        console.error("Failed to mark alert as read:", err);
      }
    }

    // 2.  
    setIsOpen(false);

    // 3.    
    if (alert.link_url) {
      navigate(alert.link_url);
    }
  };

  if (!user) {
    return null; //      
  }

  return (
    <div style={{ position: 'relative', marginRight: '20px' }}>
      {/*     */}
      <button onClick={toggleDropdown} style={styles.bellButton}>
        <BellIcon />
        {/*     */}
        {unreadCount > 0 && (
          <span style={styles.badge}>{unreadCount}</span>
        )}
      </button>

      {/*   */}
      {isOpen && (
        <div style={styles.dropdownMenu}>
          <div style={styles.dropdownHeader}> </div>
          {loading ? (
            <div style={styles.alertItem}> ...</div>
          ) : alerts.length === 0 ? (
            <div style={styles.alertItem}>  .</div>
          ) : (
            <div style={styles.alertsContainer}>
              {alerts.map(alert => (
                <div 
                  key={alert.id} 
                  style={{...styles.alertItem, ...(alert.is_read ? styles.alertRead : styles.alertUnread)}}
                  onClick={() => handleAlertClick(alert)}
                >
                  <p style={styles.alertMessage}>{alert.message}</p>
                  <small style={styles.alertTime}>{timeSince(alert.created_at)}</small>
                </div>
              ))}
            </div>
          )}
        </div>
      )}
    </div>
  );
};

//   CSS(card, btn )     
const styles = {
  bellButton: {
    position: 'relative',
    background: 'transparent',
    border: 'none',
    cursor: 'pointer',
    color: '#333', // (App.js     )
  },
  badge: {
    position: 'absolute',
    top: '-5px',
    right: '-10px',
    background: 'red',
    color: 'white',
    borderRadius: '50%',
    width: '20px',
    height: '20px',
    fontSize: '12px',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    fontWeight: 'bold',
  },
  dropdownMenu: {
    position: 'absolute',
    top: '40px', //  
    right: 0,
    width: '350px',
    maxHeight: '400px',
    overflow: 'hidden',
    backgroundColor: 'white',
    border: '1px solid #ddd',
    borderRadius: '8px',
    boxShadow: '0 4px 12px rgba(0,0,0,0.1)',
    zIndex: 1000,
    display: 'flex',
    flexDirection: 'column',
  },
  dropdownHeader: {
    padding: '12px 16px',
    fontWeight: 'bold',
    fontSize: '16px',
    borderBottom: '1px solid #eee',
  },
  alertsContainer: {
    maxHeight: '340px', // (maxHeight - headerHeight)
    overflowY: 'auto',
  },
  alertItem: {
    padding: '12px 16px',
    borderBottom: '1px solid #f0f0f0',
    cursor: 'pointer',
  },
  alertUnread: {
    backgroundColor: '#f8f9fa', //    
  },
  alertRead: {
    backgroundColor: 'white',
    color: '#888', //   
  },
  alertMessage: {
    margin: 0,
    fontSize: '14px',
    lineHeight: 1.4,
  },
  alertTime: {
    fontSize: '12px',
    color: '#666',
    marginTop: '4px',
  }
};

export default NotificationBell;