import React, { useState, useEffect } from 'react';
import { useNavigate, useParams, useLocation } from 'react-router-dom';
import { apiPostForm, apiGet } from '../../api/api';

const CreatePost = ({ user }) => {
    const navigate = useNavigate();
    const location = useLocation();
    
    // URL   
    // : /create-post/general -> boardType="general"
    // : /create-post/clan/5 -> boardId="5" (  ID)
    const { boardType, boardId } = useParams();

    // [] URL boardId  ' ' .
    const isClanMode = !!boardId;

    // 2. State 
    const [boards, setBoards] = useState([]); //    
    const [selectedBoardId, setSelectedBoardId] = useState(""); 
    const [title, setTitle] = useState("");
    const [content, setContent] = useState("");
    const [imageFile, setImageFile] = useState(null);
    const [isAnonymous, setIsAnonymous] = useState(false);
    const [error, setError] = useState("");

    // 3.     
    useEffect(() => {
        if (!isClanMode) {
            apiGet("/boards/")
                .then(data => {
                    setBoards(data);
                    if (data.length > 0) setSelectedBoardId(data[0].id);
                })
                .catch(err => console.error("   :", err));
        }
    }, [isClanMode]);

    const handleSubmit = async (e) => {
        e.preventDefault();
        setError("");

        if (!title.trim() || !content.trim()) {
            setError('  .');
            return;
        }

        const formData = new FormData();
        formData.append('title', title);
        formData.append('content', content);
        formData.append('is_anonymous', isAnonymous);
        
        if (imageFile) {
            //     'image_url'  'image'    
            //  DRF 'image' 'file' . views.py   serializer .
            formData.append('file', imageFile); 
        }

        //  []    
        if (isClanMode) {
            // [CASE 1]  : 'clan_board_id' 
            formData.append('clan_board_id', boardId);
        } else {
            // [CASE 2]  : 'board' ( ID) 
            if (!selectedBoardId) {
                setError("  .");
                return;
            }
            formData.append('board', selectedBoardId);
        }

        try {
            //  PostCreateView /     
            const response = await apiPostForm('/boards/posts/', formData);

            console.log(" :", response);
            alert(" .");

            //  
            if (response.id) {
                navigate(`/post/${response.id}`);
            } else {
                // ID   ()
                navigate(isClanMode ? -1 : "/boards");
            }

        } catch (err) {
            console.error("  :", err);
            let errorMessage = "  .";
            const errorDetail = err.response?.data?.detail || err.message;

            if (typeof errorDetail === 'string') {
                errorMessage = errorDetail;
            } else if (Array.isArray(errorDetail) && errorDetail[0]?.msg) {
                errorMessage = errorDetail[0].msg;
            }
            setError(errorMessage);
        }
    };

    return (
        <div style={{ maxWidth: '800px', margin: 'auto', padding: '20px' }}>
            <h2 className="page-title">
                {isClanMode ? '  ' : '  '}
            </h2>
            
            {isClanMode && (
                <div style={{ padding: '10px', background: '#e3f2fd', color: '#01579b', borderRadius: '5px', marginBottom: '15px' }}>
                     <strong> </strong> .
                </div>
            )}

            <form onSubmit={handleSubmit} className="card">
                {/*       */}
                {!isClanMode && boards.length > 0 && (
                    <div style={{ marginBottom: '15px' }}>
                        <label> </label>
                        <select 
                            value={selectedBoardId} 
                            onChange={(e) => setSelectedBoardId(e.target.value)}
                            className="input-field"
                        >
                            {boards.map(b => (
                                <option key={b.id} value={b.id}>{b.name}</option>
                            ))}
                        </select>
                    </div>
                )}

                <div style={{ marginBottom: '15px' }}>
                    <label></label>
                    <input
                        type="text"
                        value={title}
                        onChange={(e) => setTitle(e.target.value)}
                        required
                        className="input-field"
                        placeholder=" "
                    />
                </div>

                <div style={{ marginBottom: '15px' }}>
                    <label></label>
                    <textarea
                        value={content}
                        onChange={(e) => setContent(e.target.value)}
                        required
                        className="input-field"
                        style={{ height: "200px", resize: 'vertical' }}
                        placeholder=" "
                    />
                </div>

                <div style={{ marginBottom: '15px' }}>
                    <label> </label>
                    <input
                        type="file"
                        accept="image/*"
                        onChange={(e) => setImageFile(e.target.files[0])}
                        className="input-field"
                        style={{ padding: '5px' }}
                    />
                </div>

                <div style={{ marginBottom: '15px' }}>
                    <label style={{ display: 'flex', alignItems: 'center', cursor: 'pointer' }}>
                        <input
                            type="checkbox"
                            checked={isAnonymous}
                            onChange={(e) => setIsAnonymous(e.target.checked)}
                            style={{ marginRight: '8px' }}
                        />
                         
                    </label>
                </div>

                {error && <p style={{ color: 'red', textAlign: 'center' }}>{error}</p>}
                
                <button type="submit" className="btn btn-primary" style={{ width: "100%", padding: '10px', fontSize: '1.1em' }}>
                    
                </button>
            </form>
        </div>
    );
};

export default CreatePost;