// frontend/src/features/profile/MyPosts.js
import React, { useState, useEffect } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { apiGet } from '../../api/api';

const MyPosts = ({ user }) => {
    const [posts, setPosts] = useState([]);
    const [loading, setLoading] = useState(true);
    const navigate = useNavigate();

    useEffect(() => {
        // ---  [] API  ---
        const fetchMyPosts = async () => {
            if (!user) {
                navigate('/login'); //    
                return;
            }
            try {
                setLoading(true);
                // 1: /my-posts/ API 
                const data = await apiGet('/boards/my-posts/');
                setPosts(data);
            } catch (error) {
                console.error("     :", error);
                // (401    api.js  )
            } finally {
                setLoading(false);
            }
        };

        fetchMyPosts();
        // ---  [] ---
    }, [user, navigate]);

    // ---  []      ---
    if (loading) {
        return <div className="container" style={{padding: '20px'}}> ...</div>;
    }

    if (posts.length === 0) {
        return (
            <div className="container" style={{padding: '20px'}}>
                <button onClick={() => navigate(-1)} className="btn btn-secondary" style={{marginBottom: '20px'}}> </button>
                <h2>  </h2>
                <div className="card" style={{padding: '40px', textAlign: 'center', color: '#888'}}>
                      .
                </div>
            </div>
        );
    }
    // ---  [] ---

    return (
        <div className="container" style={{padding: '20px'}}>
            <button onClick={() => navigate(-1)} className="btn btn-secondary" style={{marginBottom: '20px'}}> </button>
            <h2>   ({posts.length})</h2>
            
            {/* ---  []    --- */}
            <div className="list-group">
                {posts.map(post => (
                    <Link 
                        key={post.id} 
                        to={`/posts/${post.id}`} 
                        className="list-group-item list-group-item-action"
                    >
                        <div className="d-flex w-100 justify-content-between">
                            <h5 className="mb-1">{post.title}</h5>
                            <small>{new Date(post.created_at).toLocaleDateString('ko-KR')}</small>
                        </div>
                        <p className="mb-1" style={{
                            overflow: 'hidden',
                            textOverflow: 'ellipsis',
                            whiteSpace: 'nowrap',
                            color: '#555'
                        }}>
                            {post.content || '( )'}
                        </p>
                        <small>
                             {post.likes_count ?? 0} &nbsp; 
                             {post.comments_count ?? 0}
                        </small>
                    </Link>
                ))}
            </div>
            {/* ---  [] --- */}
        </div>
    );
};

export default MyPosts;