import React, { useState, useEffect, useCallback } from 'react';
import { apiGet, apiPostForm } from '../../api/api';

const SupportPage = ({ user }) => {
  const [type, setType] = useState('inquiry');
  const [title, setTitle] = useState('');
  const [content, setContent] = useState('');
  const [myTickets, setMyTickets] = useState([]);

  const fetchMyTickets = useCallback(async () => {
    try {
      const data = await apiGet(`/support/my/${user.nickname}`);
      setMyTickets(data);
    } catch (err) {
      console.error("  :", err);
    }
  }, [user.nickname]);

  useEffect(() => {
    fetchMyTickets();
  }, [fetchMyTickets]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    const formData = new FormData();
    formData.append('type', type);
    formData.append('title', title);
    formData.append('content', content);
    formData.append('user_nickname', user.nickname);
    
    try {
      await apiPostForm('/support/create', formData);
      alert(type === 'feedback' ? ' !' : ' !');
      setTitle('');
      setContent('');
      fetchMyTickets();
    } catch (err) {
      alert(' ');
    }
  };

  return (
    <div style={{ maxWidth: '800px', margin: 'auto', padding: '20px' }}>
      <h2>/</h2>
      
      <div className="card">
        <form onSubmit={handleSubmit}>
          <div style={{ marginBottom: '15px' }}>
            <label>
              <input 
                type="radio" 
                value="inquiry" 
                checked={type === 'inquiry'}
                onChange={(e) => setType(e.target.value)}
              />
              
            </label>
            <label style={{ marginLeft: '20px' }}>
              <input 
                type="radio" 
                value="feedback"
                checked={type === 'feedback'}
                onChange={(e) => setType(e.target.value)}
              />
              
            </label>
          </div>
          
          <input
            type="text"
            placeholder=""
            value={title}
            onChange={(e) => setTitle(e.target.value)}
            required
            className="input-field"
          />
          
          <textarea
            placeholder=" "
            value={content}
            onChange={(e) => setContent(e.target.value)}
            required
            style={{ width: '100%', minHeight: '150px', padding: '10px' }}
          />
          
          <button type="submit" className="btn btn-primary">
            
          </button>
        </form>
      </div>

      <h3 style={{ marginTop: '30px' }}>  </h3>
      {myTickets.length === 0 ? (
        <p>  .</p>
      ) : (
        myTickets.map(ticket => (
        <div key={ticket.id} className="card" style={{ marginBottom: '10px' }}>
            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
            <h4 style={{ margin: '10px 0' }}>{ticket.title}</h4>
            <span style={{ 
                background: ticket.status === 'answered' ? '#4caf50' : '#ff9800',
                color: 'white',
                padding: '4px 12px',
                borderRadius: '4px',
                fontSize: '0.8em'
            }}>
                {ticket.status === 'answered' ? '' : ''}
            </span>
            </div>
            <p>{ticket.content}</p>
            <small>{new Date(ticket.created_at).toLocaleDateString()}</small>
            
            {ticket.replies && ticket.replies.length > 0 && ticket.replies.map(reply => (
            <div key={reply.id} style={{ 
                marginTop: '15px',
                padding: '10px',
                background: '#e3f2fd',
                borderRadius: '5px',
                borderLeft: '3px solid var(--primary-color)'
            }}>
                <strong style={{ color: 'var(--primary-color)' }}> :</strong>
                <p style={{ margin: '10px 0' }}>{reply.content}</p>
                <small style={{ color: '#666' }}>
                : {new Date(reply.created_at).toLocaleDateString()}
                </small>
            </div>
            ))}
        </div>
        ))
      )}
    </div>
  );
};

export default SupportPage;