// [ ] src/features/clan/ClanDetail.js
import React, { useEffect, useState, useCallback } from "react";
import { useParams, useNavigate, Link } from "react-router-dom"; // Link 
import { apiGet, apiPost, apiDelete, apiPostForm } from "../../api/api"; // apiPost  import  .
import Calendar from 'react-calendar';
import 'react-calendar/dist/Calendar.css';
import { useAlert } from "../../context/AlertContext";
import './ClanCalendar.css';
import Linkify from '../../components/Linkify';

//   -  
const AnnounceForm = ({ user, clanId, isOwner, onPosted }) => {
  const [open, setOpen] = useState(false);
  const [title, setTitle] = useState("");
  const [content, setContent] = useState("");

  // isOwner prop (isClanAdmin ) true    
  if (!isOwner) return null;

  const postAnnounce = async () => {
    if (!title.trim() || !content.trim()) {
      alert("   .");
      return;
    }

    try {
      //  [ 3]  API  . 
      // 1. apiPostForm (FormData) -> apiPost (JSON)
      // 2. URL: `/announcements` -> `/announcements/create/` ( urls.py )
      // 3. payload:   ( request.user  )
      await apiPost(`/clans/${clanId}/announcements/create/`, {
        title: title.trim(),
        content: content.trim()
      });
      //  [ 3] 

      //      
      setOpen(false);
      setTitle("");
      setContent("");
      onPosted?.();
      alert(" !");
    } catch (e) {
      console.error(e);
      alert(e?.response?.data?.detail || "  ");
    }
  };

  return (
    <div style={{ margin: "16px 0" }}>
      {!open ? (
        <button
          onClick={() => setOpen(true)}
          className="btn btn-primary"
          style={{ width: '100%' }}
        >
           
        </button>
      ) : (
        <div style={{ border: "1px solid #eee", padding: 12, borderRadius: 8 }}>
          <div style={{ marginBottom: 8 }}>
            <div style={{ fontSize: 12, color: "#666" }}></div>
            <input
              value={title}
              onChange={(e) => setTitle(e.target.value)}
              placeholder="  "
              style={{ width: "100%", padding: "8px", border: "1px solid #ddd", borderRadius: "4px" }}
            />
          </div>
          <div style={{ marginBottom: 8 }}>
            <div style={{ fontSize: 12, color: "#666" }}></div>
            <textarea
              value={content}
              onChange={(e) => setContent(e.target.value)}
              placeholder="  "
              rows={4}
              style={{
                width: "100%",
                padding: "8px",
                border: "1px solid #ddd",
                borderRadius: "4px",
                resize: "vertical"
              }}
            />
          </div>
          <div style={{ display: "flex", gap: 8 }}>
            <button
              onClick={postAnnounce}
              className="btn btn-primary"
              disabled={!title.trim() || !content.trim()}
            >
              
            </button>
            <button
              onClick={() => {
                setOpen(false);
                setTitle("");
                setContent("");
              }}
              className="btn btn-secondary"
            >
              
            </button>
          </div>
        </div>
      )}
    </div>
  );
};

//  
const JoinRequests = ({ user, isOwner, clan, onAction }) => { // isOwner isClanAdmin  
  if (!isOwner) return null;
  const pending = (clan.join_requests || []).filter((r) => r.status === "pending");

  const approve = async (reqId) => {
    try {
      // [API  ] 
      //  API  '/clans/requests/{reqId}/approve' ,
      //    API  `/clans/{clan_id}/join-requests/{id}/` . (POST  UPDATE/PUT)
      //    ,    API    .
      await apiPost(`/clans/requests/${reqId}/approve?nickname=${encodeURIComponent(user.nickname)}`);
      onAction?.();
    } catch (e) {
      console.error(e);
      alert(" ");
    }
  };
  const reject = async (reqId) => {
    try {
      // [API  ] ( )
      await apiPost(`/clans/requests/${reqId}/reject?nickname=${encodeURIComponent(user.nickname)}`);
      onAction?.();
    } catch (e) {
      console.error(e);
      alert(" ");
    }
  };

  const approveAll = async () => {
    if (!window.confirm(`${pending.length}    ?`)) return;
    try {
      //  API(`/clans/{clan.id}/approve-all`)    .
      const res = await apiPost(`/clans/${clan.id}/approve-all?nickname=${encodeURIComponent(user.nickname)}`);
      alert(res.message);
      onAction?.();
    } catch (e) {
      console.error(e);
      alert("  .");
    }
  };

  if (pending.length === 0) return null;

  return (
    <div style={{ marginTop: 16 }}>
      <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: 8 }}>
        <h3 style={{ margin: 0 }}> </h3>
        <button
          onClick={approveAll}
          style={{ padding: '4px 8px', fontSize: '0.8em', background: '#28a745', color: 'white' }}
          className="btn"
        >
           
        </button>
      </div>
      <ul style={{ paddingLeft: 18, listStyle: 'none', margin: 0 }}>
        {pending.map((r) => (
          <li key={r.id} style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '8px 0', borderBottom: '1px solid #f0f0f0' }}>
            <span>{r.user?.nickname || '  '}</span>
            <div>
              <button onClick={() => approve(r.id)} style={{ marginLeft: 8, fontSize: '0.8em', padding: '3px 6px' }}>
                
              </button>
              <button onClick={() => reject(r.id)} style={{ marginLeft: 6, fontSize: '0.8em', padding: '3px 6px' }}>
                
              </button>
            </div>
          </li>
        ))}
      </ul>
    </div>
  );
};

//  
const MemberListModal = ({ user, clan, isOwner, onKicked, onClose }) => { // isOwner isClanAdmin  
  const kick = async (targetNickname) => {
    if (!window.confirm(`${targetNickname}  ?`)) return;
    try {
      // [API  ]
      //  API DELETE `/clans/{clan.id}/members/{nickname}` ,
      //   API POST `/clans/{clan_id}/kick/{user_id}/` . (   ID )
      //    .
      await apiDelete(
        `/clans/${clan.id}/members/${encodeURIComponent(
          targetNickname
        )}?nickname=${encodeURIComponent(user.nickname)}`
      );
      onKicked?.();
    } catch (e) {
      console.error(e);
      alert(" ");
    }
  };

  return (
    <div className="modal-overlay" onClick={onClose}>
      <div className="modal-content member-list-modal" onClick={(e) => e.stopPropagation()}>
        <div className="modal-header">
          <h3> ({(clan.members || []).length})</h3>
          <button onClick={onClose} className="close-button">&times;</button>
        </div>
        <div className="modal-body">
          <ul style={{ padding: 0, listStyle: 'none', margin: 0 }}>
            {(clan.members || []).map((m) => (
              <li key={m.nickname} style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '10px 0', borderBottom: '1px solid #f0f0f0' }}>
                <span>
                  {m.nickname}
                  {m.nickname === clan.owner?.nickname && (
                    <span style={{ marginLeft: 6, fontSize: 12, color: "blue", fontWeight: 'bold' }}>()</span>
                  )}
                  {/*  []   (admins  )  */}
                  {clan.admins && clan.admins.some(admin => admin.nickname === m.nickname) && m.nickname !== clan.owner?.nickname && (
                    <span style={{ marginLeft: 6, fontSize: 12, color: "green", fontWeight: 'bold' }}>()</span>
                  )}
                  {/*  []  */}
                </span>
                {isOwner && m.nickname !== clan.owner?.nickname && (
                  <button onClick={() => kick(m.nickname)} className="btn btn-danger" style={{ fontSize: '0.8em', padding: '3px 8px' }}>
                    
                  </button>
                )}
              </li>
            ))}
          </ul>
        </div>
      </div>
    </div>
  );
};

//  
const ClanCalendar = ({ user, clanId, isOwner, events, onAction }) => { // isOwner isClanAdmin  
  const [date, setDate] = useState(new Date());
  const [showForm, setShowForm] = useState(false);
  const [title, setTitle] = useState("");
  const [description, setDescription] = useState("");

  const handleCreateEvent = async () => {
    if (!title.trim()) return alert("  .");
    try {
      const utcDate = new Date(Date.UTC(date.getFullYear(), date.getMonth(), date.getDate()));
      // [API  ]  API(`/clans/{clanId}/events`)     3 .
      await apiPost(`/clans/${clanId}/events`, {
        title, description, date: utcDate.toISOString(),
      }, { params: { nickname: user.nickname } });
      alert(" .");
      onAction();
      setShowForm(false);
      setTitle("");
      setDescription("");
    } catch (err) {
      alert(err.response?.data?.detail || "  ");
    }
  };

  // ( ... ClanCalendar    ... )
  const handleDeleteEvent = async (eventId) => {
    if (!window.confirm("  ?")) return;
    try {
      await apiDelete(`/clans/events/${eventId}?nickname=${user.nickname}`);
      alert(" .");
      onAction();
    } catch (err) {
      alert(err.response?.data?.detail || "  ");
    }
  };

  const getEventsForDate = (d) => {
    return (events || []).filter(e => {
      const eventDate = new Date(e.date);
      return eventDate.getFullYear() === d.getFullYear() &&
        eventDate.getMonth() === d.getMonth() &&
        eventDate.getDate() === d.getDate();
    });
  }

  return (
    <div style={{ marginTop: 20, borderTop: '1px solid #eee', paddingTop: 10 }}>
      <h3> </h3>
      <div style={{ display: 'flex', gap: '20px', flexWrap: 'wrap' }}>
        <div className="clan-calendar-container">
          <Calendar
            onChange={setDate}
            value={date}
            tileContent={({ date, view }) => {
              if (view === 'month' && getEventsForDate(date).length > 0) {
                return <div style={{ height: '8px', width: '8px', background: 'red', borderRadius: '50%', margin: 'auto', marginTop: '4px' }}></div>;
              }
            }}
          />
          {isOwner && <button onClick={() => setShowForm(!showForm)} className="btn btn-secondary" style={{ marginTop: 10, width: '100%' }}>{showForm ? '' : '+   '}</button>}
        </div>
        <div style={{ flex: 1, minWidth: '250px' }}>
          <h4>{date.toLocaleDateString()} </h4>
          {getEventsForDate(date).length === 0 ? (
            <p>   .</p>
          ) : (
            <ul>
              {getEventsForDate(date).map(e => (
                <li key={e.id}>
                  <strong>{e.title}</strong>
                  {e.description && <p style={{ margin: '4px 0', color: '#666' }}>{e.description}</p>}
                  {isOwner && <button onClick={() => handleDeleteEvent(e.id)}></button>}
                </li>
              ))}
            </ul>
          )}
        </div>
      </div>

      {isOwner && showForm && (
        <div style={{ border: "1px solid #eee", padding: 12, borderRadius: 8, marginTop: 10 }}>
          <h4>{date.toLocaleDateString()}   </h4>
          <input value={title} onChange={e => setTitle(e.target.value)} placeholder=" " style={{ width: '100%', marginBottom: 8, padding: 8 }} />
          <textarea value={description} onChange={e => setDescription(e.target.value)} placeholder=" ()" style={{ width: '100%', marginBottom: 8, padding: 8, height: 60 }} />
          <button onClick={handleCreateEvent}></button>
        </div>
      )}
    </div>
  );
};


//  
const ClanDetail = ({ user, onUpdateUser, onLogout }) => {
  const { clanId } = useParams();
  const navigate = useNavigate();
  const [clan, setClan] = useState(null);
  const [loading, setLoading] = useState(true);
  const [boardName, setBoardName] = useState("");
  const { showAlert } = useAlert();
  const [showMemberListModal, setShowMemberListModal] = useState(false);

  const fetchClan = useCallback(async () => {
    try {
      setLoading(true);
      const data = await apiGet(`/clans/${clanId}/`);
      setClan(data);
    } catch (e) {
      if (e.response && e.response.status === 401) {
        alert(" .  .");
        onLogout();
      } else {
        alert("   .");
      }
    } finally {
      setLoading(false);
    }
  }, [clanId, onLogout]);

  useEffect(() => {
    fetchClan();
  }, [fetchClan]);

  const handleCreateBoard = async () => {
    if (!boardName.trim()) return alert("  .");
    try {
      // [API  ] 3  ()
      await apiPost(`/clans/${clanId}/boards/`, { name: boardName });
      alert("  .");
      setBoardName("");
      fetchClan();
    } catch (err) {
      alert(err.response?.data?.detail || "  ");
    }
  };

  const handleDeleteAnnouncement = async (announcementId) => {
    showAlert(
      "  ",
      "   ?",
      async () => {
        try {
          // [API  ] 3  ()
          await apiDelete(`/clans/announcements/${announcementId}?nickname=${user.nickname}`);
          alert(" .");
          fetchClan();
        } catch (err) {
          alert(err.response?.data?.detail || "  .");
        }
      }
    );
  };

  const handleDeleteBoard = async (boardId, boardName) => {
    showAlert(
      " ",
      `'${boardName}'   ?\n    .`,
      async () => {
        try {
          // [API  ] 3  ()
          await apiDelete(`/clans/boards/${boardId}?nickname=${encodeURIComponent(user.nickname)}`);
          showAlert("", " .", fetchClan, false);
        } catch (e) {
          showAlert("", e.response?.data?.detail || " ", () => { }, false);
        }
      }
    );
  };

  //  [ 1] 'isOwner'  'isClanAdmin'   
  const isOwner = user && clan && clan.owner?.nickname === user.nickname;
  // 'clan.admins'    user  
  const isAdmin = user && clan && clan.admins && clan.admins.some(admin => admin.nickname === user.nickname);
  // [] (Owner) (Admin) isClanAdmin = true
  const isClanAdmin = isOwner || isAdmin;
  //  [ 1] 

  const isMember = user && clan && (clan.members || []).some((m) => m.nickname === user.nickname);

  const requestJoin = async () => {
    if (!user) return alert(" .");
    try {
      // [API  ]  API(`/clans/{clanId}/join/`) POST  GET.
      //   API POST `/clans/{pk}/join_request/` .
      const res = await apiPost(`/clans/${clanId}/join/`);

      alert(res?.message || "  .");
      fetchClan();
    } catch (e) {
      console.error(e);
      alert(e?.response?.data?.detail || "  ");
    }
  };


  if (loading) return <div style={{ padding: 20 }}></div>;
  if (!clan) return <div style={{ padding: 20 }}>  .</div>;

  const handleApprove = async () => {
    if (!window.confirm("  ?")) return;
    try {
      await apiPost(`/clans/manage/${clan.id}/approve/`);
      alert(".");
      fetchClan();
    } catch (e) {
      alert(" : " + (e.response?.data?.detail || e.message));
    }
  };

  const handleReject = async () => {
    if (!window.confirm("  ?")) return;
    try {
      await apiPost(`/clans/manage/${clan.id}/reject/`);
      alert(".");
      navigate('/clans');
    } catch (e) {
      alert(" : " + (e.response?.data?.detail || e.message));
    }
  };

  return (
    <div style={{ padding: 20 }}>
      {clan.status === 'pending' && (
        <div style={{ background: '#fff3cd', color: '#856404', padding: '15px', borderRadius: '5px', marginBottom: '20px', border: '1px solid #ffeeba' }}>
          <h3 style={{ margin: '0 0 10px 0' }}>   </h3>
          <p style={{ margin: 0 }}>      .      .</p>
          {user?.role === 'OPERATOR' && (
            <div style={{ marginTop: '15px' }}>
              <button onClick={handleApprove} className="btn" style={{ marginRight: '10px', background: '#28a745', color: 'white', padding: '8px 16px' }}></button>
              <button onClick={handleReject} className="btn" style={{ background: '#dc3545', color: 'white', padding: '8px 16px' }}></button>
            </div>
          )}
        </div>
      )}

      <div className="page-header">
        <button onClick={() => navigate(-1)} className="btn btn-secondary"> </button>

        {isMember && (
          <Link to={`/clans/${clanId}/dashboard`}>
            <button className="btn btn-primary">  </button>
          </Link>
        )}

        {!isMember && (
          <button onClick={requestJoin}>  </button>
        )}
      </div>

      <div style={{ marginBottom: '15px' }}>
        <h2 style={{ margin: 0 }}>{clan.name}</h2>
        {clan.description && (
          <div style={{ color: "#666", marginTop: 4, marginBottom: '10px' }}>
            <Linkify>{clan.description}</Linkify>
          </div>
        )}

        <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center", gap: '10px' }}>
          {isMember && (
            <Link to={`/chats/clan/${clanId}`} style={{ flex: 1 }}>
              <button className="btn btn-primary" style={{ width: '100%' }}> </button>
            </Link>
          )}
          {isMember && (
            <Link to={`/clans/${clanId}/rooms`} style={{ flex: 1, textDecoration: 'none' }}>
              <button className="btn btn-secondary" style={{ width: '100%' }}>
                 
              </button>
            </Link>
          )}
        </div>
      </div>

      {isMember && (
        <>
          <Link to={`/clans/${clanId}/activity`} style={{ textDecoration: 'none' }}>
            <button
              className="btn"
              style={{ width: '100%', background: '#f8f9fa', border: '1px solid #dee2e6', color: '#212529', textAlign: 'left', padding: '12px 15px' }}
            >
              <span style={{ fontWeight: 'bold' }}>  </span>
              <span style={{ float: 'right', color: '#6c757d' }}>&gt;</span>
            </button>
          </Link>

          <button
            onClick={() => setShowMemberListModal(true)}
            className="btn"
            style={{ width: '100%', marginTop: '15px', background: '#f8f9fa', border: '1px solid #dee2e6', color: '#212529', textAlign: 'left', padding: '12px 15px' }}
          >
            <span style={{ fontWeight: 'bold' }}>  </span>
            <span style={{ float: 'right', color: '#6c757d' }}>{(clan.members || []).length} &gt;</span>
          </button>
        </>
      )}

      <div style={{ borderTop: '1px solid #eee', marginTop: 10, paddingTop: 10 }}>
        {/*  [ 2] isOwner -> isClanAdmin   */}
        <JoinRequests user={user} isOwner={isClanAdmin} clan={clan} onAction={fetchClan} />
        {/*  [ 2]  */}
      </div>

      {isMember && (
        <>
          <div style={{ borderTop: '1px solid #eee', marginTop: 10, paddingTop: 10 }}>
            <h3 style={{ marginTop: 20, marginBottom: 15 }}> </h3>
            {(clan.announcements || []).length === 0 ? (
              <div className="card" style={{ padding: '20px', textAlign: 'center', color: '#888', background: '#f8f9fa' }}>
                   .
              </div>
            ) : (
              <div style={{ display: 'flex', flexDirection: 'column', gap: '12px' }}>
                {(clan.announcements || [])
                  .slice()
                  .sort((a, b) => new Date(b.created_at) - new Date(a.created_at))
                  .map((a, index) => (
                    <div key={a.id} style={{
                      background: 'var(--primary-color-light)',
                      borderLeft: '4px solid var(--primary-color)',
                      borderRadius: '8px',
                      padding: '16px',
                      boxShadow: '0 1px 3px rgba(0,0,0,0.05)'
                    }}>
                      <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '8px' }}>
                        <span style={{ fontWeight: 'bold', fontSize: '1.1em', color: 'var(--text-color)' }}>
                          {a.title}
                        </span>

                        <div style={{ display: 'flex', alignItems: 'center', gap: '10px' }}>
                          {index === 0 && (
                            <span style={{
                              background: 'var(--primary-color)', color: 'white',
                              padding: '2px 8px', borderRadius: '12px',
                              fontSize: '0.7em', fontWeight: 'bold'
                            }}>
                              NEW
                            </span>
                          )}
                          {/*  [ 2] isOwner -> isClanAdmin   */}
                          {isClanAdmin && (
                            <button
                              onClick={() => handleDeleteAnnouncement(a.id)}
                              style={{
                                background: 'transparent', border: 'none', color: '#dc3545',
                                cursor: 'pointer', fontSize: '0.8em', fontWeight: 'bold', padding: '0'
                              }}
                            >
                              
                            </button>
                          )}
                          {/*  [ 2]  */}
                        </div>
                      </div>
                      <div style={{ color: '#333', marginBottom: '12px', lineHeight: '1.6' }}>
                        <Linkify>{a.content}</Linkify>
                      </div>
                      <div style={{ fontSize: '0.8em', color: "#666", textAlign: 'right' }}>
                        {new Date(a.created_at).toLocaleString('ko-KR')}
                      </div>
                    </div>
                  ))}
              </div>
            )}

            {/*  [ 2] isOwner -> isClanAdmin   */}
            <AnnounceForm user={user} clanId={clan.id} isOwner={isClanAdmin} onPosted={fetchClan} />
            {/*  [ 2]  */}
          </div>

          <div style={{ borderTop: '1px solid #eee', marginTop: 20, paddingTop: 10 }}>
            <h3> </h3>
            {/*  [ 2] isOwner -> isClanAdmin   */}
            {isClanAdmin && (
              <div className="card" style={{ marginBottom: '15px' }}>
                <h4 style={{ marginTop: 0 }}>  </h4>
                <input value={boardName} onChange={e => setBoardName(e.target.value)} placeholder=" " className="input-field" style={{ margin: 0 }} />
                <button onClick={handleCreateBoard} className="btn btn-primary" style={{ marginTop: '10px' }}></button>
              </div>
            )}
            {/*  [ 2]  */}

            {(clan.boards || []).length === 0 ? (
              <p>   .</p>
            ) : (
              (clan.boards || []).map(board => (
                <div key={board.id} style={{ display: 'flex', alignItems: 'center', gap: '10px', marginBottom: '10px' }}>
                  {/*  [] Link state={{ clanId: clan.id }}   */}
                  <Link
                    to={`/boards/clan/${board.id}`}
                    state={{ clanId: clan.id }} // []   !
                    style={{ textDecoration: 'none', flex: 1 }}
                  >
                    <div className="card" style={{ margin: 0 }}>
                      {board.title}
                    </div>
                  </Link>
                  {/*  [ ]  */}
                  {isClanAdmin && (
                    <button
                      onClick={() => handleDeleteBoard(board.id, board.title)}
                      className="btn btn-danger"
                      style={{ padding: '8px 12px' }}
                    >
                      
                    </button>
                  )}
                  {/*  [ 2]  */}
                </div>
              ))
            )}
          </div>

          {/*  [ 2] isOwner -> isClanAdmin   */}
          <ClanCalendar user={user} clanId={clan.id} isOwner={isClanAdmin} events={clan.events || []} onAction={fetchClan} />
          {/*  [ 2]  */}
        </>
      )}
      {showMemberListModal && (
        <MemberListModal
          user={user}
          clan={clan}
          //  [ 2] isOwner -> isClanAdmin  
          isOwner={isClanAdmin}
          //  [ 2] 
          onKicked={fetchClan}
          onClose={() => setShowMemberListModal(false)}
        />
      )}
    </div>
  );
};

export default ClanDetail;