import axios from "axios";

// =================================================================
// 1.    URL 
// =================================================================

// []  Django     . ( '/' )
const DEPLOYED_BACKEND_URL = "https://bandicon-test.onrender.com";

//   
const isDevelopment = process.env.NODE_ENV === 'development';

// []      export 
export const API_BASE_SERVER = isDevelopment ? "http://127.0.0.1:8000" : DEPLOYED_BACKEND_URL;

// []  
export const ADMIN_TOKEN = "abc123-VERY-LONG-RANDOM";

console.log(` API : ${isDevelopment ? '(Local)' : '(Production)'}`);
console.log(`  : ${API_BASE_SERVER}`);

// =================================================================
// 2. Axios  
// =================================================================

// (1)   API 
const api = axios.create({
    baseURL: `${API_BASE_SERVER}/api/v1`, // API_BASE_SERVER 
    headers: {
        'Content-Type': 'application/json',
    },
    withCredentials: true,
});

// (2) / API 
const adminApi = axios.create({
    baseURL: `${API_BASE_SERVER}/api/v1`, // API_BASE_SERVER 
    headers: {
        'Content-Type': 'application/json',
    }
});

// =================================================================
// 3.   (/ )
// =================================================================

// [ ] JWT   
api.interceptors.request.use(
    (config) => {
        const token = localStorage.getItem('accessToken');
        if (token) {
            config.headers['Authorization'] = `Bearer ${token}`;
        }
        return config;
    },
    (error) => Promise.reject(error)
);

// [ ] 401     
api.interceptors.response.use(
    (response) => response,
    async (error) => {
        const originalRequest = error.config;

        // 401  +   +    
        if (
            error.response?.status === 401 && 
            !originalRequest._retry && 
            !originalRequest.url.includes('/users/token/')
        ) {
            originalRequest._retry = true;
            
            const refreshToken = localStorage.getItem('refreshToken');
            
            if (refreshToken) {
                try {
                    console.log("  .   ...");
                    
                    // adminApi    (  )
                    const response = await adminApi.post('/users/token/refresh/', { 
                        refresh: refreshToken 
                    });
                    
                    const { access } = response.data;
                    
                    localStorage.setItem('accessToken', access);
                    console.log("   !");
                    
                    //      
                    originalRequest.headers['Authorization'] = `Bearer ${access}`;
                    return api(originalRequest);
                    
                } catch (refreshError) {
                    console.error("    ( ):", refreshError);
                    handleLogout();
                    return Promise.reject(refreshError);
                }
            } else {
                console.log("   . .");
                handleLogout();
            }
        }
        
        return Promise.reject(error);
    }
);

//  
const handleLogout = () => {
    localStorage.removeItem('accessToken');
    localStorage.removeItem('refreshToken');
    localStorage.removeItem('bandicon_user');
    // window.location.href = '/login'; //    
};

// =================================================================
// 4. API   (export)
// =================================================================

export const apiGet = async (url) => {
    try {
        const res = await api.get(url);
        return res.data;
    } catch (err) {
        throw err;
    }
};

export const apiPost = async (url, body, config = {}) => {
    try {
        const res = await api.post(url, body, config);
        return res.data;
    } catch (err) {
        throw err;
    }
};

export const apiPostForm = async (url, formData) => {
    try {
        const res = await api.post(url, formData, {
            headers: { "Content-Type": "multipart/form-data" }
        });
        return res.data;
    } catch (err) {
        throw err;
    }
};

export const apiPut = async (url, body) => {
    try {
        const res = await api.put(url, body);
        return res.data;
    } catch (err) {
        throw err;
    }
};

export const apiDelete = async (url) => {
    try {
        const res = await api.delete(url);
        return res.data;
    } catch (err) {
        throw err;
    }
};

// [ API]
export const adminGet = async (url) => {
    try {
        const res = await adminApi.get(url, {
            headers: { "X-Admin-Token": ADMIN_TOKEN }
        });
        return res.data;
    } catch (err) {
        throw err;
    }
};

export const adminPost = async (url, body) => {
    try {
        const res = await adminApi.post(url, body || {}, {
            headers: { "X-Admin-Token": ADMIN_TOKEN }
        });
        return res.data;
    } catch (err) {
        throw err;
    }
};

export const adminPostForm = async (url, formData) => {
    try {
        const res = await adminApi.post(url, formData, {
            headers: { 
                "Content-Type": "multipart/form-data",
                "X-Admin-Token": ADMIN_TOKEN
            }
        });
        return res.data;
    } catch (err) {
        throw err;
    }
};

export const adminDelete = async (url) => {
    try {
        const res = await adminApi.delete(url, {
            headers: { "X-Admin-Token": ADMIN_TOKEN }
        });
        return res.data;
    } catch (err) {
        throw err;
    }
};

export const adminPut = async (url, body) => {
    try {
        const res = await adminApi.put(url, body || {}, {
            headers: { "X-Admin-Token": ADMIN_TOKEN }
        });
        return res.data;
    } catch (err) {
        throw err;
    }
};

export default api;