import React, { useEffect, useState, useCallback } from "react"; //  useCallback 
import { useNavigate } from "react-router-dom";
import { apiGet } from "../../api/api";

function MyRooms({ user }) {
  const [rooms, setRooms] = useState([]);
  const navigate = useNavigate();

  // ---  [] useCallback   ---
  const fetchMyRooms = useCallback(async () => {
    if (!user?.nickname) return;
    try {
      // ---  [] URL   ( request.user ) ---
      const res = await apiGet(`/rooms/my/`);
      setRooms(res);
    } catch (err) {
      console.error("    ", err);
    }
  }, [user]); //  user    
  // ---  [] ---
    
  useEffect(() => {
    fetchMyRooms();
    const interval = setInterval(fetchMyRooms, 5000); // 5 
    return () => clearInterval(interval);
  }, [fetchMyRooms]); //  fetchMyRooms   

  return (
    <div>
      <h2 className="page-title" style={{ textAlign: 'left', marginBottom: '20px' }}>  </h2>
      {rooms.length > 0 ? (
        // ---  [] grid   ---
        <div style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fill, minmax(300px, 1fr))', gap: '20px' }}>
          {rooms.map((room) => (
            <div
              key={room.id}
              className="card" //  [] card  
              style={{ 
                cursor: "pointer", 
                backgroundColor: room.ended ? '#f9f9f9' : 'white', 
                opacity: room.ended ? 0.7 : 1,
                borderLeft: room.confirmed && !room.ended ? '4px solid var(--primary-color)' : '4px solid transparent'
              }}
              onClick={() => navigate(`/rooms/${room.id}`)}
            >
              <h3 style={{ margin: '0 0 10px 0' }}>{room.title} {room.ended && "()"}</h3>
              <p style={{ margin: '0 0 5px 0' }}>{room.song} - {room.artist}</p>
              {/* ---  [] /   --- */}
              <p style={{ fontSize: '0.9em', color: '#555', margin: '10px 0 0 0', paddingTop: '10px', borderTop: '1px solid #eee' }}>
                {room.manager_nickname === user.nickname 
                  ? <strong style={{ color: 'var(--primary-color)' }}>[]</strong> 
                  : `[: ${room.sessions.find(s => s.participant_nickname === user.nickname)?.session_name || '  '}]`
                }
              </p>
            </div>
          ))}
        </div>
      ) : (
        <div className="card" style={{ padding: '40px', textAlign: 'center', color: '#888' }}>
          <p style={{ margin: 0 }}>   .</p>
        </div>
      )}
    </div>
  );
}

export default MyRooms;