import React, { useState, useEffect, useCallback } from 'react';
import { apiGet, apiPostForm } from '../../api/api';

const AdminSupportPage = ({ user }) => {
  const [feedbacks, setFeedbacks] = useState([]);
  const [filter, setFilter] = useState('all'); // all, feedback, inquiry
  const [selectedItem, setSelectedItem] = useState(null);
  const [replyContent, setReplyContent] = useState('');

  const fetchFeedbacks = useCallback(async () => {
    try {
      const url = filter === 'all' 
        ? `/admin/feedbacks?nickname=${user.nickname}`
        : `/admin/feedbacks?type_filter=${filter}&nickname=${user.nickname}`;
    const data = await apiGet(url);
    //    ,    
    const sortedData = data.sort((a, b) => {
    if (a.status === 'pending' && b.status === 'answered') return -1;
    if (a.status === 'answered' && b.status === 'pending') return 1;
    return new Date(b.created_at) - new Date(a.created_at); //   
    });
    setFeedbacks(sortedData);
    } catch (err) {
      alert(' .');
    }
  }, [filter, user.nickname]);

  useEffect(() => {
    fetchFeedbacks();
  }, [fetchFeedbacks]);

  const handleReply = async (feedbackId) => {
    if (!replyContent.trim()) {
      alert('  .');
      return;
    }

    const formData = new FormData();
    formData.append('content', replyContent);
    formData.append('admin_nickname', user.nickname);

    try {
      await apiPostForm(`/admin/feedback/${feedbackId}/reply`, formData);
      alert(' .');
      setReplyContent('');
      setSelectedItem(null);
      fetchFeedbacks();
    } catch (err) {
      alert('  ');
    }
  };

  return (
    <div style={{ maxWidth: '1000px', margin: 'auto', padding: '20px' }}>
      <h2>/ </h2>
      
      {/*  */}
      <div style={{ marginBottom: '20px' }}>
        <button 
          className={filter === 'all' ? 'btn btn-primary' : 'btn btn-secondary'}
          onClick={() => setFilter('all')}
          style={{ marginRight: '10px' }}
        >
          
        </button>
        <button 
          className={filter === 'inquiry' ? 'btn btn-primary' : 'btn btn-secondary'}
          onClick={() => setFilter('inquiry')}
          style={{ marginRight: '10px' }}
        >
          
        </button>
        <button 
          className={filter === 'feedback' ? 'btn btn-primary' : 'btn btn-secondary'}
          onClick={() => setFilter('feedback')}
        >
          
        </button>
      </div>

      {/*  */}
      <div style={{ display: 'grid', gap: '15px' }}>
        {feedbacks.map(item => (
          <div key={item.id} className="card">
            <div style={{ display: 'flex', justifyContent: 'space-between', marginBottom: '10px' }}>
              <h4 style={{ margin: 0 }}>{item.title}</h4>
              <div>
                <span style={{ 
                  background: item.type === 'feedback' ? '#4caf50' : '#2196f3',
                  color: 'white',
                  padding: '2px 8px',
                  borderRadius: '4px',
                  fontSize: '0.8em',
                  marginRight: '10px'
                }}>
                  {item.type === 'feedback' ? '' : ''}
                </span>
                <span style={{ 
                  background: item.status === 'answered' ? '#4caf50' : '#ff9800',
                  color: 'white',
                  padding: '2px 8px',
                  borderRadius: '4px',
                  fontSize: '0.8em'
                }}>
                  {item.status === 'answered' ? '' : ''}
                </span>
              </div>
            </div>
            
            <p style={{ margin: '10px 0' }}>{item.content}</p>
            <small>: {item.user.nickname} | {new Date(item.created_at).toLocaleDateString()}</small>
            
            {/*    */}
            {item.replies && item.replies.map(reply => (
              <div key={reply.id} style={{ 
                marginTop: '15px',
                padding: '10px',
                background: '#f0f0f0',
                borderRadius: '5px'
              }}>
                <strong> (: {reply.admin.nickname}):</strong>
                <p>{reply.content}</p>
                <small>{new Date(reply.created_at).toLocaleDateString()}</small>
              </div>
            ))}
            
            {/*   () */}
            {item.type === 'inquiry' && item.status !== 'answered' && (
              <div style={{ marginTop: '15px' }}>
                {selectedItem === item.id ? (
                  <div>
                    <textarea
                      placeholder=" "
                      value={replyContent}
                      onChange={(e) => setReplyContent(e.target.value)}
                      style={{ width: '100%', minHeight: '100px', padding: '10px' }}
                    />
                    <div style={{ marginTop: '10px' }}>
                      <button 
                        onClick={() => handleReply(item.id)}
                        className="btn btn-primary"
                        style={{ marginRight: '10px' }}
                      >
                         
                      </button>
                      <button 
                        onClick={() => { setSelectedItem(null); setReplyContent(''); }}
                        className="btn btn-secondary"
                      >
                        
                      </button>
                    </div>
                  </div>
                ) : (
                  <button 
                    onClick={() => setSelectedItem(item.id)}
                    className="btn btn-primary"
                  >
                     
                  </button>
                )}
              </div>
            )}
          </div>
        ))}
      </div>
    </div>
  );
};

export default AdminSupportPage;