import React, { useEffect, useState, useCallback } from "react";
import { Link, useNavigate } from "react-router-dom";
import { apiGet, apiPost } from "../../api/api";

//  [] user prop  , useAuth()  . 
const ClanHome = ({user}) => {

  const [clans, setClans] = useState([]);
  const navigate = useNavigate();
  const [showCreate, setShowCreate] = useState(false);
  const [name, setName] = useState("");
  const [description, setDescription] = useState("");
  const [error, setError] = useState("");


  
  const load = useCallback(async () => {
    console.log("   ");
    try {
      const data = await apiGet("/clans/"); // []   /clans/  
      console.log("   :", data);
      setClans(data || []);
    } catch (e) {
      console.error("  :", e.response?.status, e.response?.data);
      if (e.response?.status === 401) {
        alert("  !");
      }
    }
  }, []);

  useEffect(() => {
    load();
  }, [load]);

  const create = async (e) => {
    e.preventDefault();
    setError("");
    if (!name.trim()) return setError("  .");

    try {
      //  []     ( views.py request.user ) 
      const newClan = await apiPost(`/clans/`, { name, description });
      //  [] 
      
      setShowCreate(false);
      setName("");
      setDescription("");
      await load(); 
      alert(" !");
      navigate(`/clans/${newClan.id}`);
    } catch (err) {
      setError(err.response?.data?.detail || "  ");
    }
  };

  //  []    
  const requestJoin = async (e, clanId) => {
    e.preventDefault(); // Link   ( ) 
    e.stopPropagation(); //   

    if (!user) return alert(" .");
    
    try {
      //  ClanViewSet 'join_request' @action 
      const res = await apiPost(`/clans/${clanId}/join_request/`); 
      alert(res?.detail || "  .");
      load(); //   (     )
    } catch (e) {
      alert(e.response?.data?.detail || "  ");
    }
  };
  //  [] 

  
  return (
    <div style={{ maxWidth: 800, margin: "auto" }}>
      <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
        <h2 className="page-title" style={{textAlign: 'left', margin: 0}}> </h2>
        {/* [] user     ( ) */}
        {user && (
          <button className="btn btn-primary" onClick={() => setShowCreate(!showCreate)}>
            {showCreate ? '' : '+  '}
          </button>
        )}
      </div>

      {showCreate && (
        <form onSubmit={create} className="card" style={{ marginTop: 20 }}>
          {/* ... (     ) ... */}
          <h3 style={{marginTop: 0}}>  </h3>
          <input
            value={name}
            onChange={(e) => setName(e.target.value)}
            placeholder=" "
            className="input-field"
          />
          <textarea
            value={description}
            onChange={(e) => setDescription(e.target.value)}
            placeholder="  ()"
            className="input-field"
            rows={3}
          />
          {error && <p style={{ color: "red" }}>{error}</p>}
          <button className="btn btn-primary" type="submit"></button>
        </form>
      )}

      <div style={{marginTop: 20}}>
        {clans.map((clan) => {
          
          //  []     
          // user.pk (  ID)
          // clan.owner.id ( ID)
          // clan.members (ClanSerializer 'members'    - [] )
          
          // [] ClanSerializer 'members'    serializers.py .
          // (    'members'  )
          const isOwner = user && clan.owner?.id === user.pk;
          const isMember = user && clan.members && clan.members.some(member => member.id === user.pk);
          //  [] 

          return (
            <Link key={clan.id} to={`/clans/${clan.id}`} style={{textDecoration: 'none', color: 'inherit'}}>
              <div className="card" style={{marginBottom: 10}}>
                <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center'}}>
                  <h3 style={{margin: 0}}>{clan.name}</h3>
                  <span style={{fontSize: '0.9em', color: '#666'}}>: {clan.owner?.nickname ?? ' '}</span>
                </div>
                <p style={{color: '#666', marginTop: '10px'}}>{clan.description || '  .'}</p>
                <div style={{
                    marginTop: '15px', borderTop: '1px solid var(--light-gray)', 
                    paddingTop: '10px', fontSize: '0.9em',
                    display: 'flex', justifyContent: 'space-between', alignItems: 'center' // []    flex
                }}>
                  {/*  [] member_count   */}
                  <span>
                    : {clan.member_count ?? 0}
                  </span>
                  {/*  []  */}

                  {/*  []    */}
                  {user && !isOwner && !isMember && (
                    <button 
                      className="btn btn-secondary" 
                      onClick={(e) => requestJoin(e, clan.id)}
                    >
                      
                    </button>
                  )}
                  {/*  []  */}
                </div>
              </div>
            </Link>
          )
        })}
      </div>
    </div>
  );
};


export default ClanHome;