// VAPID  - Firebase     
const VAPID_KEY = "BOWGrbXEHh5BwBlGLRls0yBrz03KG2-piLj2phBUknGkRXDnfizoTkPy7nawz8CecfjOZeK0cW_9VNqCB0mteNk";

export const requestForToken = async (nickname) => {
  try {
    console.log("     ");
    
    // 1.  
    if (Notification.permission === "denied") {
      console.log("   ");
      alert("   .    .");
      return;
    }

    // 2.  
    const permission = await Notification.requestPermission();
    console.log("  :", permission);
    
    if (permission !== "granted") {
      console.log("   ");
      return;
    }

    // 3. Service Worker  
    let registration;
    try {
      registration = await navigator.serviceWorker.getRegistration('/');
      if (!registration) {
        console.log(" Service Worker  ");
        registration = await navigator.serviceWorker.register('/firebase-messaging-sw.js', {
          scope: '/'
        });
      } else {
        console.log("  Service Worker ");
      }
      
      //  
      await registration.update();
      
      // SW  
      if (registration.installing) {
        await new Promise((resolve) => {
          registration.installing.addEventListener('statechange', (e) => {
            if (e.target.state === 'activated') {
              resolve();
            }
          });
        });
      }
    } catch (swError) {
      console.error(" Service Worker  :", swError);
      return;
    }

    // 4. FCM  
    console.log(" FCM   ");
    const currentToken = await getToken(messaging, {
      vapidKey: VAPID_KEY,
      serviceWorkerRegistration: registration
    });

    if (currentToken) {
      console.log(" FCM   :", currentToken.substring(0, 20) + "...");
      
      // 5.   
      try {
        const formData = new FormData();
        formData.append("token", currentToken);
        formData.append("nickname", nickname);
        
        const response = await apiPostForm("/register-device", formData);
        console.log("    :", response);
        
        return currentToken;
      } catch (apiError) {
        console.error("    :", apiError);
      }
    } else {
      console.log(" FCM   ");
    }
  } catch (err) {
    console.error(" FCM   :", err);
  }
};

//  
export const checkFirebaseStatus = () => {
  console.log(" Firebase  :");
  console.log("-  :", !!app);
  console.log("-  :", !!messaging);
  console.log("-  :", Notification.permission);
  console.log("- Service Worker :", 'serviceWorker' in navigator);
  console.log("- HTTPS :", window.location.protocol === 'https:');
};